* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body {
    font-family: Roboto, sans-serif;
    background-color: rgba(255, 255, 255, 1);
    color: rgba(0, 0, 0, 1);

}

address {
    margin-bottom: 9px;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
    width: 100%;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding-left: 0;
}